/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box; }

/**
   * Make all elements from the DOM inherit from the parent box-sizing
   * Since `*` has a specificity of 0, it does not override the `html` value
   * making all elements inheriting from the root box-sizing value
   * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
   */
*,
*::before,
*::after {
  box-sizing: inherit; }

/**
   * Basic styles for links
   */
a {
  text-decoration: none; }

/**
 * Basic typography style for copy text
 */
.l-grid {
  display: grid;
  grid-column: 1/-1;
  column-gap: 25px;
  grid-template-columns: repeat(10, 100px); }

.l-grid--header {
  grid-template-areas: "name name . . . nav nav nav nav nav";
  grid-template-rows: 100px; }
  .l-grid--header .c-name {
    grid-area: name; }
  .l-grid--header .c-nav {
    grid-area: nav; }

.l-grid--billboard {
  grid-column: 1/8;
  grid-auto-flow: row;
  grid-template-rows: auto auto auto; }
  .l-grid--billboard .c-billboard__title {
    grid-column: 1/4; }
  .l-grid--billboard .c-billboard__blurb {
    grid-column: 3/8; }
  .l-grid--billboard .c-billboard__link {
    grid-column: 3/4; }

.l-grid--section .c-section__heading {
  grid-column: 1/-1; }

.l-grid--section .c-section__content {
  grid-column: 6/-1; }

